/*Resets*/
ul{
    padding:0;
    margin:0;
}

li{
    list-style:none;
}

html,
body,
#root,
.app-container{
    height: 100%;
}


/* Color Theme Swatches in Hex */
.red { color: #F23C50; }
.redBG{ background-color: #F23C50; }
.yellow{ color: #FFCB05; }
.yellowBG{ background-color: #FFCB05; }
.white{ color: #FFFFFF; }
.whiteBG{ background-color: #FFFFFF; }
.lgtBlue{ color: #4AD9D9; }
.lgtBlueBG{ background-color: #4AD9D9; }
.drkBlue{ color: #112f41; }
.drkBlueBG{ background-color: #112f41; }
.black{color:#333333;}
.blackBG{ background-color: #333333; }

/*Typography*/

h1, h2, h3, h4, h5, h6{
    font-family: 'Heebo', sans-serif;
}

p, a{
    font-family: 'Libre Franklin', sans-serif;
    font-size:16px;
    line-height: 1rem;
}

h1{
    font-size: 48px;
    font-weight:bold;
}
h2{
    font-size: 36px;
    font-weight:bold;
}
h3{
    font-size: 36px;
    font-weight: normal;
}
h4{
    font-size:24px;
    font-weight:normal;
}
h5{
    font-size:18px;
    font-weight:normal;
}
h6{
    font-size:14px;
    font-weight: normal;
}
